
import React from 'react';
import {Link} from "react-router-dom";

const Footer = (props) => {

    const hideitem = {
        display: (props.page === 'about') ? "none" : ''
    };

    return ( 
        <footer id="footer">
        <div className="contact-bottom">
            <div className="contact">
                <div className="get_in_touch">
                    <div className="contact_line"></div>
                    <div className="text bluecolored">Get in touch</div>
                </div>
                <div className="phone">+1 450 672-0401</div>
                <div className="email">contact@vmn.net</div>
                <div className="address">
                    304 - 6200 Taschereau
                    <br/>Brossard, Quebec
                    <br/>J4W 3J8
                    <br/>Canada    
                </div>
            </div>
        </div>
        <div className="useful_links">
            <div className="title bluecolored">Useful links</div>
            <div className="links">
                <div className="link" style={hideitem}><a href="#our_products" className="smooth-goto">Products</a></div>
                <div className="link"><a href="#our_clients" className="smooth-goto">Clients</a></div>
                <div className="link"><Link to="/about">About</Link></div>
                <div className="link"><a href="#footer" className="smooth-goto">Contact</a></div>
            </div>
        </div>
        <div className="keep_in_touch">
            <div className="title bluecolored">Keep in Touch</div>
            <div className="get_in_social">
                <div className="social_icons">
                    <div>
                        <a href="https://www.facebook.com/VisicomMedia/">
                            <img alt="facebook" src="data:image/png;base64," />
                        </a>
                    </div>
                    <div>
                        <a href="https://twitter.com/visicommedia?lang=en">
                            <img alt="twitter" src="data:image/png;base64," />
                        </a>
                    </div>
                    <div>
                        <a href="https://www.linkedin.com/company/visicom-media">
                            <img alt="linkedin" src="data:image/png;base64," />
                        </a>
                    </div>
                </div>
            </div>
            <div className="copyright">&copy; 2020 Visicom Media Inc. All Rights Reserved. <a href="https://vmn.net/privacy">Privacy Policy</a></div>
        </div>
    </footer>
     );
}
 
export default Footer;